h1, h2, h3, h4, h5, h6, p, blockquote, pre, abbr, address, cite, code, del, dfn, em, img, ins, kbd, q, samp, small, strong, sub, sup, var, b, i, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, figure, footer, header, hgroup, menu, nav, section, time, mark, audio, video {
  margin: 0;
  padding: 0;
  
}
body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#Footer{
    padding: 20px;
    text-align: right;
    padding-left: 20px;
    margin-top: 20px;
    background-color: lightgray;
}
#navBar{
    background-color: #728FCE;
    padding-left: 10px;
    border-style: solid;
    border-width: 2px;
}
#title{
    font-size: 50px;
    margin-bottom: 20px;
    margin-top: 10px;
}
#buttonGroup{
    display: flex;
    flex-direction: row;
    justify-content: space-around;
}
@media screen and (max-width: 540px) {
    #buttonGroup {
        flex-direction: column;
    }
    .button{
        align-self: center;
    }
}
.button{
    margin-right: 5px;
    padding: 20px;
    width: 200px;
    text-align: center;
    
}
.active{
    background-color: lightblue;
}
.button:hover { 
    background-color: lightblue;
}
.logo{
    display: flex;
    flex-direction:row;
    align-items:center; 
    margin-bottom:20px;
}

.Page{
    min-height: 70vh;
    margin: auto;
}

h1,h2, h3{
    margin-top: 10px;
    margin-bottom: 10px;
}

pre {
    background: #eee;
    padding: 1rem;
    overflow: auto;
    border-radius: 3px;
    max-width: 80ch;
  }
  
  pre code {
      text-align: left;
      white-space: pre;
      word-spacing: normal;
      word-break: normal;
      word-wrap: normal;
      line-height: 1.5;
  }
.pluginCategory{
    background-color: lightgray;
    text-align: center;
    padding: 20px 0;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    margin-bottom: 20px;
    border-radius: 20px;
    flex-wrap: wrap;
    justify-content: center;
}

.legacyFormat img{
    padding: 20px;
}

.Page {
    margin-left: 10px;
    margin-right: 10px;
}
